.error input, .error textarea {
  border: 2px solid red
}

.error span {
  color: red;
}

.formSummaryError {
  color: red;
  margin-bottom: 10px;
  font-size: 14px;
  font-style: italic;
}

.loginForm {
  width: 250px;
  padding: 10px;
}

.loginForm input {
  padding: 5px;
  border-radius: 5px;
}

.fieldRow {
  margin-bottom: 10px;
}

.errorText {
  font-size: 12px;
}

.formBtn {
  min-width: 150px;
  padding: 5px 10px;
  background-color: #e5ebf1;
  cursor: pointer;
  border: 1px solid #ccc;
  border-radius: 5px;
  color: #55677D;
  margin-top: 10px;
}

.captcha {
  margin-bottom: 10px;
}