.header{
    height: 42px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #4a76a8;
    padding: 5px 10px;
}
.header img{
    width: 65px;
    border-radius: 50%;
}
.login{
    color: #fff;
}
.login a{
    color: white;
}
.login button{
    color: #fff;
    background-color: transparent;
    border: none;
    cursor: pointer;
}