.postsBlock{
    padding: 10px;
    background-color: #fff;
    border-radius: 10px;
    border: 1px solid #ccc;
}
.postsBlock h3{
    margin-top: 10px;
}
.postsBlock textarea{
    max-width: 320px;
    width: 100%;
    padding: 5px;
    border-radius: 5px;
}
.postsBlock button{
    min-width: 150px;
    padding: 5px 10px;
    background-color: #e5ebf1;
    cursor: pointer;
    border: 1px solid #ccc;
    border-radius: 5px;
    color: #55677D;
    margin-top: 10px;
}
.myPosts{
    margin-top: 10px;
}
