.user {
    width: 200px;
    height: 200px;
}

.userPhoto {
    position: relative;
}

.userPhoto:hover.userPhoto label {
    opacity: 1;
}

.user img {
    width: 200px;
}

.userPhoto input {
    display: none;
}

.user label {
    background-color: rgba(0, 0, 0, .7);
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    cursor: pointer;
    text-align: center;
    padding: 5px;
    color: #fff;
    opacity: 0;
    transition: .3s;
}

.descriptionBlock {
    display: flex;
    margin-bottom: 15px;
}

.profileImage {
    width: 100%;
    height: 300px;
}

.profileImage img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.contact {
    padding-left: 10px;
}

.contact.profileDataFormItem, .profileDataFormItem {
    margin-bottom: 10px;
}

.contact.profileDataFormItem span, .profileDataFormItem span {
    display: inline-block;
    margin-bottom: 5px;
}

.status {
    padding: 7px;
    background-color: #fff;
    text-align: center;
    margin-top: 5px;
    border-radius: 10px;
    border: 1px solid #ccc;
}

.status input {
    border: none;
    outline: none;
    font-size: 15px;
    width: 100%;
    text-align: center;
}

.profileInner {
    padding: 10px;
    background-color: #fff;
    margin-left: 15px;
    border-radius: 10px;
    border: 1px solid #ccc;
    width: 100%;
}

.profileData {
    position: relative;
}

.editBtn,
.saveBtn {
    position: absolute;
    right: 0;
    top: 0;
    min-width: 70px;
    padding: 5px 10px;
    background-color: #e5ebf1;
    cursor: pointer;
    border: 1px solid #ccc;
    border-radius: 5px;
    color: #55677D;
}

.profileForm {
    position: relative;
    display: flex;
}

.profileForm input, .profileForm textarea {
    width: 100%;
    padding: 5px;
    border-radius: 5px;
    border: 1px solid #000;
}

.profileForm input[type='checkbox'] {
    width: auto;
}

.name {
    font-weight: 500;
    font-size: 22px;
    margin-bottom: 10px;
}

.profileDataItem {
    color: #939393;
    font-style: italic;
}

.profileDataItem span {
    display: inline-block;
    margin-bottom: 3px;
    margin-right: 3px;
    font-weight: 500;
    color: #000;
}

.mainInfo {
    flex-basis: 50%;
    padding-right: 10px;
}

.contacts {
    flex-basis: 50%;
    padding-left: 10px;
    padding-right: 20px;
}