.selectedPage{
  font-weight: 700;
  color: #4a76a8;
}
.pagination{
  margin-bottom: 10px;
}
.pagination span{
  display: inline-block;
  margin-right: 5px;
  cursor: pointer;
}
.pageNumber{
  padding: 3px;
}
.pageNumber.selectedPage{
  font-weight: 700;
  border-color: black;
}
.prevBtn,
.nextBtn{
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  vertical-align: bottom;
}
.prevBtn:disabled,
.nextBtn:disabled{
  cursor: auto;
  opacity: 0;
}
.prevBtn img,
.nextBtn img{
  width: 20px;
}
