.avatar{
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.like img{
  width: 20px;
}
.like span{
  vertical-align: top;
}
.like{
  align-self: flex-end;
}
.post{
  border-bottom: 1px solid lightgray;
  display: flex;
  justify-content: space-between;
  padding: 5px;
  margin-bottom: 5px;
}
.postText{
  padding-left: 5px;
  padding-bottom: 5px;
  display: inline-block;
  transform: translateY(-10px);
}